:root {
  --mdc-theme-primary: #00e676 !important;
  --mdc-theme-secondary: #27272F !important;
  --mdc-theme-on-primary: #ffffff !important;
  --mdc-theme-on-secondary: #ffffff !important;
  --drawer-color: #33333D;
  --divider-color: #505060;
  --bg-image: url("https://images8.alphacoders.com/903/903580.png")
; }

.atomic-app-bar {
  z-index: 1;
  margin: 0 0;
  background-color: var(--mdc-theme-secondary) !important; }

.atomic-drawer {
  position: fixed !important;
  height: 100%;
  background-color: var(--drawer-color) !important; }

.atomic-drawer .mdc-drawer__content {
  background-color: var(--drawer-color) !important; }

.atomic-drawer-subheader {
  font-size: 12px !important;
  color: #80d8ff !important; }

.atomic-drawer .mdc-drawer__content * {
  color: #00e5ff; }
  .atomic-drawer .mdc-drawer__content *::before, .atomic-drawer .mdc-drawer__content *::after {
    background-color: white; }

@-webkit-keyframes move-right {
  from {
    padding-left: 0px; }
  to {
    padding-left: calc(239px); } }

@-moz-keyframes move-right {
  from {
    padding-left: 0px; }
  to {
    padding-left: calc(239px); } }

@keyframes move-right {
  from {
    padding-left: 0px; }
  to {
    padding-left: calc(239px); } }

@-webkit-keyframes move-left {
  from {
    padding-left: calc(239px); }
  to {
    padding-left: 0px; } }

@-moz-keyframes move-left {
  from {
    padding-left: calc(239px); }
  to {
    padding-left: 0px; } }

@keyframes move-left {
  from {
    padding-left: calc(239px); }
  to {
    padding-left: 0px; } }

.atomic-auth-button {
  color: var(--mdc-theme-primary) !important; }

.atomic-menu {
  background-color: var(--drawer-color) !important; }

.atomic-menu .mdc-list-item::before, .atomic-menu .mdc-list-item::after {
  background-color: white !important; }

.atomic-menu .mdc-list-item {
  color: white !important; }

.atomic-content-wrapper {
  -webkit-animation-delay: 0;
  -webkit-animation-duration: 250ms;
  -webkit-animation-name: move-right;
  -moz-animation-delay: 0;
  -moz-animation-duration: 250ms;
  -moz-animation-name: move-right;
  animation-delay: 0;
  animation-duration: 250ms;
  animation-name: move-right;
  /*animation-name: move-right;
  animation-duration: 250ms;*/
  padding-left: calc(239px);
  margin-bottom: 0px;
  background-image: var(--bg-image) !important;
  background-size: cover;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  min-height: calc(100vh - 64px); }

.atomic-text-field :not(.mdc-text-field--disabled):not(.mdc-line-ripple) {
  background-color: var(--mdc-theme-secondary) !important; }

.atomic-text-field .mdc-floating-label {
  color: var(--mdc-theme-primary) !important; }

.atomic-text-field input {
  color: white !important; }

.atomic-search-field {
  border-radius: 5px;
  min-width: 30%;
  float: left;
  margin-left: auto;
  margin-right: 10px; }

.atomic-icon-toggle-selected {
  color: #1976d2 !important; }

.atomic-search-field .mdc-notched-outline, .atomic-search-field .mdc-notched-outline__idle {
  border-radius: 10px !important; }

.atomic-search-field input {
  font-size: 25px;
  text-align: center; }

.mdc-floating-label--float-above {
  color: var(--mdc-theme-primary) !important; }

.atomic-search-wrapper {
  margin-top: 64px;
  height: 136px;
  min-width: 100%;
  display: flex;
  align-items: center; }

.atomic-icon-radio-button {
  float: left; }

.atomic-icon-radio-group {
  display: flex;
  align-items: center; }

.atomic-search-platform-radio {
  float: left;
  margin-left: auto;
  min-height: 56px !important; }

.atomic-search-button {
  border-radius: 5px !important;
  float: left;
  margin-right: auto; }

.atomic-content {
  padding-top: 10px;
  padding-left: 20px;
  padding-right: 10px;
  padding-bottom: 10px; }

.atomic-footer {
  -webkit-box-shadow: 0px -2px 5px 0px rgba(0, 0, 0, 0.4);
  -moz-box-shadow: 0px -2px 5px 0px rgba(0, 0, 0, 0.4);
  box-shadow: 0px -2px 5px 0px rgba(0, 0, 0, 0.4); }
